<template>
  <div class="Login-box">
    <div class="head">
      <p>设置密码</p>
      <span @click="lwh_btn">跳过</span>
    </div>
    <van-form @submit="onSubmit">
      <div class="lwh_input" v-for="(item, index) in lwh_value" :key="index">
        <input
          :class="{ lwh_valueIndex: index == lwh_valueIndex }"
          :type="item.txt"
          v-model="item.cont"
          :placeholder="item.place"
          @focus="lwh_focus(index)"
          @blur="lwh_blur()"
        />
      
      </div>

      <div style="margin: 16px">
        <van-button
          round
          block
          type="info"
          native-type="submit"
          color="linear-gradient(to right, rgb(255, 146, 71),rgb(253, 88, 3))"
        >
          确定
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      lwh_value: [
        { cont: "", txt: "text", type: "name", place: "请设置登录密码" },
        { cont: "", txt: "password", type: "pass", place: "请确认登录密码" },
      ],
      lwh_valueIndex: null,
      lwh_flag: true,
      lwh_time: 59,
    };
  },
  methods: {
    onSubmit(values) {
      console.log("submit", values);
    },
    add() {
      this.$router.push({
        path: "/Wyl-Login",
      });
    },
    // 获取焦点
    lwh_focus(index) {
      this.lwh_valueIndex = index;
    },
    // 失去焦点
    lwh_blur() {
      this.lwh_valueIndex = null;
    },
    lwh_btn(){
      this.$router.push("/lwh_mess")
    }
  },
};
</script>

